*{
    margin: 0;
    padding: 0;
    list-style: none;
    font-family: Arial, Helvetica, sans-serif;
    color: white;
}
body{
    background-color: black;
}
div{
    height: 40px;
    margin-top: 15px;
    margin-bottom: -10px;
}
h1{
    /* margin-top:15px; */
text-align: center;
/* font-size: 15px; */
animation: ani 0.9s linear infinite alternate-reverse;
/* border: 2px solid; */
}
@keyframes ani {
    from { font-size: 8px;}
    to{font-size: 16px;}
}
h2{
    text-align: center;
}

ul{
    margin-top: 20px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
}

ul li{
    background-color: rgb(102, 102, 218);
    width: 280px;
    height: 150px;
    text-align: center;
    border-radius: 5px;
    margin: 10px;
    color: white;
    transition: 0.2s linear;
}
ul li:hover{
padding: 8px;
margin-top: 4px;
}
ul li:hover p{
    font-size: 20px;
}
ul li:hover span{
    font-size: 10px;
}
ul li:hover p:nth-child(2){
    font-size: 52px;
}
ul li:hover .lastupd{
    font-size: 39px !important;
}
ul li:nth-child(2){background-color: rgb(2, 151, 2);}
ul li:nth-child(3){background-color: rgb(192, 195, 3);}
ul li:nth-child(4){background-color: rgb(250, 111, 30);}
/* ul li:nth-child(3){background-color: rgb(208, 211, 9);} */
ul li:nth-child(6){background-color: rgb(164, 167, 38);}
ul li p{
    font-size: 18px;
    /* margin-top: 5px; */
    padding-top: 10px;
    font-weight: 700;
    padding-bottom: 10px;
    text-transform: uppercase;
    transition: 0.2s linear;
}
ul li span{
    font-size: 8px;
    transition: 0.2s linear;
}
ul li p:nth-child(2){
    margin-top: -10px;
    font-size: 48px;
    text-shadow: 5px 3px 8px rgb(35, 35, 35);
    transition: 0.2s linear;
}
.lastupd{
    font-size: 35px !important;
    transition: 0.2s linear;
}
